import React, { useEffect, useState } from "react";
import "./css/app.css";
import "./css/command.css";
import { Link } from "react-router-dom";
import info from "./assets/info.png";
import loadingImage from "./assets/loading.gif";
import axios from "axios";
import home from "./assets/home.png";

export default function Command({ match }) {
    const argumentTypes = {
        "3": {
            name: "String",
            description: "Any string",
            image: ["https://cd.n23.io/sites/kashimadocs/TFjzo8N.png"]
        },
        "4": {
            name: "Integer",
            description: "Any integer between -2^53 and 2^53",
            image: ["https://cd.n23.io/sites/kashimadocs/K74qsYW.png"]
        },
        "5": {
            name: "Boolean",
            description: "Boolean, true / false",
            image: ["https://cd.n23.io/sites/kashimadocs/wUpqfsy.png"]
        },
        "6": {
            name: "User",
            description: "Any guild's members",
            image: ["https://cd.n23.io/sites/kashimadocs/SNGenbN.png"]
        },
        "7": {
            name: "Channel",
            description: "Any guild's channels",
            image: ["https://cd.n23.io/sites/kashimadocs/C7wi4xX.png"]
        },
        "8": {
            name: "Role",
            description: "Any guild's roles",
            image: ["https://cd.n23.io/sites/kashimadocs/HdhvMqM.png"]
        },
        "9": {
            name: "Mentionable",
            description: "Includes users and roles",
            image: ["https://cd.n23.io/sites/kashimadocs/SNGenbN.png", "https://cd.n23.io/sites/kashimadocs/HdhvMqM.png"]
        },
        "10": {
            name: "Number",
            description: "Any double between -2^53 and 2^53",
            image: ["https://cd.n23.io/sites/kashimadocs/K74qsYW.png"]
        }
    }

    const [layer, setLayer] = useState({
        active: true,
        type: "loading",
        message: ""
    })
    const [loading, setLoading] = useState(true);
    const [invalid, setInvalid] = useState(false);
    const [data, setData] = useState({});

    useEffect(() => {
        getCommand().then(categories => {
            categories = Object.entries(categories);
            if (!categories.map(x => x[0]).includes(match.params.categoryName)) return setData({name: "Invalid category", commands: []});
            const category = categories.find(x => x[0] === match.params.categoryName)
            setData({ name: category[0], commands: category[1] })
            setLoading(false);
        })
    }, [])

    async function getCommand() {
        return new Promise(((resolve, reject) => {
            const source = axios.CancelToken.source();
            const timeout = setTimeout(() => {
                source.cancel();
                reject("Timed out");
            }, 10000);

            axios.get("https://raw.githubusercontent.com/Deivu/Kashima/master/assets/commands.json", { cancelToken: source.token }).then(async ({
                data,
                status
            }) => {
                clearTimeout(timeout);
                if (status !== 200) return resolve(await getCommand())
                resolve(data)
            }).catch(reject);
        }))
    }

    useEffect(() => {
        if (loading) setLayer({ active: true, type: "loading", message: "" })
        else setLayer({ active: false, type: "", message: "" })
    }, [loading])

    function layerHandler(data) {
        const args = argumentTypes[data.message]
        // if (data.type === "loading") return (<div className="layer">
        //     <div className="layer-main layer-main-loading centered">
        //         <img src={loadingImage} alt="loading" />
        //         <p>Loading the commands...</p>
        //     </div>
        // </div>)
        if (data.type === "args" && args) return (
            <div className="layer" onClick={async () => {
                document.getElementsByClassName("layer")[0].classList.add("layer-out");
                await new Promise(r => setTimeout(r, 230));
                setLayer({ active: false, type: "", message: "" })
            }}>
                <div className="layer-main centered">
                    <p className="layer-main-title">{args.name}</p>
                    <hr />
                    <p className="layer-main-desc">{args.description}</p>
                    <div className="layer-main-images">
                        {args.image.map((x, i) => <img src={x} alt={args.name} key={i} />)}
                    </div>
                    <p className="layer-main-hint">Click anywhere to close</p>
                </div>
            </div>)
    }

    function handleArgs(data) {
        let type = argumentTypes[data.type]
        console.log(type)

    }

    function renderTableData() {
        return data.commands.map((cmd, index) => {
            console.log(cmd)
            const { name, description } = cmd //destructuring
            return (
                <tr key={index}>
                    <td>{name}</td>
                    <td>{description}</td>
                    <td>{cmd.args && Array.isArray(cmd.args) ? cmd.args.map(x => (<a className="cmd-table-a" onClick={() => setLayer({
            active: true,
            type: "args",
            message: `${x.type}`
        })}>{x.name}</a>)) : "-"}</td>
                </tr>
            )
        })
    }

    function renderTableHeader() {
        return ["name", "description", "args"].map((key, index) => <th key={index}>{key.toUpperCase()}</th>)
    }

    return (<>
        <div className="header">
            <div className="header-content">
                <div className="header-application-name">
                    <img className="header-content-logo"
                        src="https://cd.n23.io/sites/kashimadocs/dhSvfuC.png"
                        alt="Kashima" />
                    <p className="header-content-name">Kashima Docs</p>
                </div>
                <div className="header-search-bar" />
                <Link to="/" className="header-buttons">
                    <img src={home} alt="Home" />
                </Link>
            </div>
        </div>
        <div className="main-section">
            {layer.active ? layerHandler(layer) : ""}
            <div className={"main-content"}>
                <div
                    className={"main-content-readme command-readme"}>
                    <div className="readme-title">
                        <p className="readme-title-title">{data.name ? data.name.split("").map((x, i) => i === 0 ? x.toUpperCase() : x).join("") : "Loading..."}</p>
                    </div>
                </div>
                <div className="command-table">
                    <table className="commands">
                        <tbody>
                            <tr>{loading ? "" : renderTableHeader()}</tr>
                            {loading ? "" : renderTableData()}
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    </>)
}