import "./css/app.css";
import info from "./assets/info.png";
import React, { useEffect, useState, useRef } from "react";
import loadingImage from "./assets/loading.gif";
import { Link } from "react-router-dom";
import Typical from "react-typical";
import axios from "axios";

import { Navigation, Pagination, Scrollbar, A11y, Autoplay } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/swiper-bundle.min.css'
import 'swiper/swiper.min.css'

const images = {
    "action": "https://cd.n23.io/sites/kashimadocs/nptuuYc.png",
    "config": "https://cd.n23.io/sites/kashimadocs/PNUFjlc.png",
    "game": "https://cd.n23.io/sites/kashimadocs/ehZzdSv.png",
    "general": "https://cd.n23.io/sites/kashimadocs/HB8W9bz.png",
    "developer": "https://cd.n23.io/sites/kashimadocs/JxPK16Z.png",
    "image": "https://cd.n23.io/sites/kashimadocs/dWQvg0R.png",
    "moderation": "https://cd.n23.io/sites/kashimadocs/9UgsY5W.png",
    "music": "https://cd.n23.io/sites/kashimadocs/WU8oK5I.png",
    "suggestion": "https://cd.n23.io/sites/kashimadocs/hczhcr8.png"
}

export default function App({location}) {
    const [layer, setLayer] = useState({
        active: true,
        type: "loading",
        message: ""
    })
    const [loading, setLoading] = useState(true);
    const [statistic, setStatistic] = useState({});
    const [data, setData] = useState({
        invite: "https://discord.com/api/oauth2/authorize?client_id=424137718961012737&permissions=137744477510&scope=bot%20applications.commands",
        categories: []
    });
    const swiperRef = useRef();
    const TypingAnimation = React.memo(() => {
        return <Typical
            loop={Infinity}
            wrapper="p"
            steps={[
                "Music.",
                1200,
                "Moderation.",
                1200,
                "Suggestions.",
                1200,
                "Reminders.",
                1200,
                "Images.",
                1200
            ]}
            className="readme-content-content"
        />
    }, () => true); // this line prevent re rendering

    useEffect(() => {
        collectData().catch(() => null);
        collectCommands().then(categories => {
            categories = Object.keys(categories);
            setData(x => ({ ...x, categories: categories.filter(x => x !== "developer") }))
            setLoading(false);
        })
        setTimeout(() => scrollToCommand(location), 500)
        const intv = setInterval(() => swiperRef ? swiperRef.current.swiper.slideTo(Math.floor(Math.random() * 4)) : null, 5000)
        return () => clearInterval(intv);
    }, [])

    function scrollToCommand(loc) {
        if(loc.pathname !== "/commands") return false;
        const y = document.getElementById("command");
        if(!y) return false;
        window.scrollTo(0, y.offsetTop);
        return true;
    }

    useEffect(() => {
        if (loading) setLayer({ active: true, type: "loading", message: "" })
        else setLayer({ active: false, type: "", message: "" })
    }, [loading])

    async function collectData() {
        return new Promise(async (resolve, reject) => {
            // fetch("https://aqua.kashima.moe/status")
            const { data, status } = await axios.get("https://aqua.kashima.moe/status");
            setStatistic(data)
        })
    }

    async function collectCommands() {
        return new Promise(((resolve, reject) => {
            const source = axios.CancelToken.source();
            const timeout = setTimeout(() => {
                source.cancel();
                reject("Timed out");
            }, 10000);

            axios.get("https://raw.githubusercontent.com/Deivu/Kashima/master/assets/commands.json", { cancelToken: source.token }).then(async ({
                data,
                status
            }) => {
                clearTimeout(timeout);
                if (status !== 200) return resolve(await collectCommands())
                resolve(data)
            }).catch(reject);
        }))
    }

    function layerHandler(data) {
        // if (data.type === "loading") return (<div className="layer">
        //     <div className="layer-main layer-main-loading centered">
        //         <img src={loadingImage} alt="loading"/>
        //         <p>Loading the commands...</p>
        //     </div>
        // </div>)
    }

    function categoryHandler(data, index) {
        return (<Link to={`/category/${data}`} className="category-parent" key={index}>
            <img src={images[data]} alt={data} />
            <p>{data.split("").map((x, i) => i === 0 ? x.toUpperCase() : x).join("")}</p>
        </Link>)
    }

    function statisticHandler(type) {
        console.log(type)
        return (
            <div className="statistic-child">
                <p className="statistic-child-title">{type[0].split("").map((x, i) => i === 0 ? x.toUpperCase() : x).join("")}</p>
                <p className="statistic-child-content">{type[1] ? type[1] : 0}</p>
            </div>
        )
    }

    return (<>
        <div className="header">
            <div className="header-content">
                <div className="header-application-name">
                    <img className="header-content-logo"
                        src="https://cd.n23.io/sites/kashimadocs/dhSvfuC.png"
                        alt="Kashima" />
                    <p className="header-content-name">Kashima</p>
                </div>
                <div className="header-search-bar" />
                <Link to="/about" className="header-buttons">
                    <img src={info} alt="Info" />
                </Link>
            </div>
        </div>
        <div className="main-section">
            {layer.active ? layerHandler(layer) : ""}
            <div className={"main-content"}>
                <div className="main-content-readme-bg">
                    <div className={"main-content-readme main-content-readme-home"}>
                        <div className="readme-left">
                            <div className="readme-title">
                                <p className="readme-title-title">Hi I'm Kashima!</p>
                                <p className="readme-title-desc">Your cute shipgirl bot that does a lot of things!<br /><b>Trusted by {statistic["users"] ? statistic.users.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1.') : '1.000.000'} users</b></p>
                            </div>
                            <div className="readme-content">
                                <TypingAnimation />
                                <div className="readme-content-invite-links"
                                    onClick={() => window.location = data.invite ? data.invite : "https://discord.com/api/oauth2/authorize?client_id=424137718961012737&permissions=137744477510&scope=bot%20applications.commands"}>
                                    <div className="readme-content-invite-child">
                                        <img src="https://cd.n23.io/sites/kashimadocs/QlPTUhU.png" alt="discord" />
                                        <p>Invite me!</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="readme-right">
                            <img src="https://i.imgur.com/wWXNNXR.png" alt="kashima" />
                        </div>
                    </div>
                </div>
                <div className="features">
                    <p className="main-content-title">Features</p>
                    <hr />
                    <Swiper
                        // install Swiper modules
                        ref={swiperRef}
                        modules={[Navigation, Pagination, Scrollbar, A11y, Autoplay]}
                        // spaceBetween={100}
                        slidesPerView={1}
                        className="features-parent"
                        navigation
                        autoplay
                        pagination={{ clickable: true }}
                        // autoplay={{delay: 5000}}
                        speed={1000}
                        direction={'horizontal'}
                        scrollbar={{ draggable: true }}
                        onSwiper={(swiper) => console.log(swiper)}
                        onSlideChange={() => console.log('slide change')}
                    >
                        <SwiperSlide>
                            <div className="feature-child">
                                <img src="https://cd.n23.io/sites/kashimadocs/TJkKEp9.png" alt="music" />
                                <div className="feature-child-content">
                                    <p className="feature-child-content-title">Music</p>
                                    <p>High quality music player that has a lot of unique features other bots don't have.</p>
                                </div>
                            </div>
                        </SwiperSlide>
                        <SwiperSlide>
                            <div className="feature-child">
                                <img src="https://cd.n23.io/sites/kashimadocs/GEmrRB8.png" alt="config" />
                                <div className="feature-child-content">
                                    <p className="feature-child-content-title">Moderation</p>
                                    <p>Keeps track of the things happening in your Discord server and moderate it at the same time.</p>
                                </div>
                            </div>
                        </SwiperSlide>
                        <SwiperSlide>
                            <div className="feature-child">
                                <img className="feature-child-dont-invert" src="https://cd.n23.io/sites/kashimadocs/17634.png" alt="moderation" />
                                <div className="feature-child-content">
                                    <p className="feature-child-content-title">Suggestions</p>
                                    <p>Keeps track of suggestions around your Discord server, with the ability to approve, reject and consider it while being able to notify the user that suggested it.</p>
                                </div>
                            </div>
                        </SwiperSlide>
                        <SwiperSlide>
                            <div className="feature-child">
                                <img className="feature-child-dont-invert" src="https://cd.n23.io/sites/kashimadocs/38467.png" alt="premium" />
                                <div className="feature-child-content">
                                    <p className="feature-child-content-title">Reminders</p>
                                    <p>Never forget something again. Let our Shipgirl remind it for you.</p>
                                </div>
                            </div>
                        </SwiperSlide>
                        <SwiperSlide>
                            <div className="feature-child">
                                <img className="feature-child-dont-invert" src="https://cd.n23.io/sites/kashimadocs/91623.png" alt="premium" />
                                <div className="feature-child-content">
                                    <p className="feature-child-content-title">Images</p>
                                    <p>Ever wanted a random catgirl image, pat someone, or make someone a meme? We got it as well.</p>
                                </div>
                            </div>
                        </SwiperSlide>
                    </Swiper>
                    {/* <div className="features-div">
                    </div> */}
                </div>
                <div className="categories-parent statistic-parent">
                    <p className="main-content-title">Statistics</p>
                    <hr />
                    <div className="main-content-statistic">
                        {Object.entries(statistic).filter(([key, value]) => key !== "uptime").map((x, i) => statisticHandler(x, i))}
                    </div>
                </div>
                <div className="categories-parent" id="command">
                    <p className="main-content-title">Commands</p>
                    <hr />
                    <div className="main-content-categories">
                        {data.categories && Array.isArray(data.categories) && data.categories.length ?
                            data.categories.map(categoryHandler)
                            :
                            <Link to={`/`} className="category-parent">
                                <img src={loadingImage} alt={"loading"} />
                                <p>Loading...</p>
                            </Link>
                        }
                    </div>
                </div>


                <div className="patreon-parent">
                    <p className="main-content-title">Become a <b className="patron">Patron</b>!</p>
                    <hr style={{ width: "100%" }} />
                    <p className="patreon-description">
                        Support the development of Kashima and get great Premium Features, without having to vote, and Premium Voice Servers at a reasonable price!
                    </p>
                    <div className="patreon-button" onClick={() => window.location = "https://www.patreon.com/TheWeebSaya"}>
                        Support Me!
                    </div>
                </div>

                <div className="team-parent">
                    <p className="main-content-title">Our <b className="team">Team</b></p>
                    <hr />
                    <div className="teams">
                        <div className="team-child">
                            <img src="https://cd.n23.io/sites/kashimadocs/37rreN9.png" alt="saya" />
                            <div className="team-child-content">
                                <div className="team-child-content-title">
                                    <p>Saya</p>
                                    <div className="team-child-content-tag">Head Developer</div>
                                    <div className="team-child-content-tag">Owner</div>
                                </div>
                                <p className="team-child-content-about">I'm Saya, The Lead Developer / Owner of Kashima. I'm a Visual Novel and Anime Lover type of guy. I love doing random but fun things as well as Programming, or just lazing around and doing nothing if I want to.</p>
                            </div>
                        </div>

                        <div className="team-child">
                            <img src="https://cd.n23.io/sites/kashimadocs/8nS1VGT.png" alt="takase" />
                            <div className="team-child-content">
                                <div className="team-child-content-title">
                                    <p>Takase</p>
                                    <div className="team-child-content-tag">Developer</div>
                                </div>
                                <p className="team-child-content-about">I'm Takase, a hobbyist programmer. I usually code in JavaScript and C#, and sometimes C/C++. I'm quite lazy so please bear with me!</p>
                            </div>
                        </div>

                        <div className="team-child">
                            <img src="https://cd.n23.io/sites/kashimadocs/yViVbjW.png" alt="alexa" />
                            <div className="team-child-content">
                                <div className="team-child-content-title">
                                    <p>Alyxa</p>
                                    <div className="team-child-content-tag">Developer</div>
                                </div>
                                <p className="team-child-content-about">I'm Alexa and I will think of something to put here later [insert smug anime girl here]. </p>
                            </div>
                        </div>

                        <div className="team-child">
                            <img src="https://cd.n23.io/sites/kashimadocs/ubmuPSV.png" alt="rattley" />
                            <div className="team-child-content">
                                <div className="team-child-content-title">
                                    <p>Rattley</p>
                                    <div className="team-child-content-tag">Developer</div>
                                </div>
                                <p className="team-child-content-about">I'm Rattley, also known as 0t4u or Ay3 on other platforms. I code in JavaScript or C# sometimes. SEND HEL-</p>
                            </div>
                        </div>

                        <div className="team-child">
                            <img src="https://cd.n23.io/sites/kashimadocs/NRotdsk.png" alt="takiyo" />
                            <div className="team-child-content">
                                <div className="team-child-content-title">
                                    <p>Takiyo</p>
                                    <div className="team-child-content-tag">Wiki Team</div>
                                </div>
                                <p className="team-child-content-about">I'm Takiyo, a full stack developer and also neko lover. I love to pat nekos because they are so cute.</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="back-to-top-btn" onClick={() => window.scrollTo(0, 0)} />
        </div>
        <div className="main-footer">
            <div className="first-footer">
                <div className="footer-content">
                    <p className="footer-content-title">Pages</p>
                    <Link to="/" className="footer-content-page">Home</Link><br />
                    <Link to="/about" className="footer-content-page">FAQ</Link><br />
                    <Link to="/policy" className="footer-content-page">Privacy Policy</Link>
                </div>
                <div className="footer-name">
                    <img src="https://cd.n23.io/sites/kashimadocs/dhSvfuC.png" alt="kashima" />
                    <p>Kashima</p>
                </div>
            </div>
            <div className="another-footer-content">
                <p>Find me here</p>
                <div className="another-footer-content-parent">
                    {[{
                        name: "topgg",
                        embed: "https://discordbots.org/api/widget/424137718961012737.svg",
                        redirect: "https://discordbots.org/bot/424137718961012737"
                    }, {
                        name: "BFD",
                        embed: "https://botsfordiscord.com/api/bot/424137718961012737/widget?theme=dark",
                        redirect: "https://botsfordiscord.com/bots/424137718961012737"
                    }, {
                        name: "bots-on-discord",
                        embed: "https://bots.ondiscord.xyz/bots/424137718961012737/embed?theme=dark&showGuilds=true",
                        redirect: "https://bots.ondiscord.xyz/bots/424137718961012737"
                    }, {
                        name: "discord-boats",
                        embed: "https://discord.boats/api/widget/424137718961012737",
                        redirect: "https://discord.boats/bot/424137718961012737"
                    }].map((x, i) => (<img alt={x.name} src={x.embed} onClick={() => window.location = x.redirect} key={i} />))}
                </div>
                    <p>Our partner</p>
                    <img className="partner" src="https://cdn.discordapp.com/attachments/551992888058183690/676006192723525642/Small_Banner_250x65.png" alt="erisly"/>
            </div>
            <div className="copyright">
                <p>Â©2021 The Shipgirl Project. All right reserved.</p>
            </div>
        </div>
    </>);
}